'use strict';

require('fecomponent/mobi-zepto-countdown');
require('fecomponent/mobi-jswebview');
var Wxshare = require('components/wxshare/wxshare');
var Dialog = require('components/dialog/dialog');
var template = require('fecomponent/mobi-art-template');
var globalConfig = require('fecomponent/mobi-detect-ua');

function renderData(templateId, data, $container) {
  var html = template(templateId, data);
  $container.append(html);
}

var CommonwealPage = function () {
  //0:全部；1:我的
  this.currentSwitch = 0;
};

CommonwealPage.prototype.countDown119 = function () {
  var $countDown = $('.j_CountDown');
  var serverTime = parseInt($countDown.attr('data-servertime') || Date.now());
  var endTime = parseInt($countDown.attr('data-endtime'));

  var counter = new $.countdown({
    correctDateOffset: parseInt((serverTime - Date.now()) / 1000),
    endDate: endTime / 1000,
    onUpdate: function (arr) {
      var d = arr.days;
      var h = arr.hours;
      var m = arr.minutes;

      $countDown.find('.j_CountDay').html(d);
      $countDown.find('.j_CountHour').html(h);
      $countDown.find('.j_CountMin').html(m);
    }
  });
  counter.start();
};

CommonwealPage.prototype.pageShare = function () {
  var shareData = {
    title: '达人店11·9总动员狂欢节',
    desc: '达人店11·9总动员狂欢节来啦，万店齐发，买贵包赔！！万件好货，最低1折 ！！！',
    link: window.location.href,
    imgUrl: 'https://cdn1.showjoy.com/images/81/8162111445d14d409312fed3cb086767.jpg'
  };
  Wxshare(shareData);

  if (globalConfig.Browser.versions.showjoyShopiOs || globalConfig.Browser.versions.showjoyShopAndroid) {
    $.JSBridge.registerHandler('wxShareInfo', function () {
      return shareData;
    });

    $.JSBridge.callHandler('shopc_send_shareInfo', shareData, function () {
    });

    $('.j_Share').on('click', function () {
      $.JSBridge.callHandler('c_share', shareData, function () {
      });
    });
  } else {
    $('.j_Share').on('click', function () {
      Dialog.wxShare();
    });
  }
};

CommonwealPage.prototype.listRank = function () {
  var _self = this;
  _self.currentSwitch = 0;
  $('.j_MyTeam').text('我的战队');
  var $rankTable = $('.j_RankTable');
  var $myTeamInfo = $('.j_MyTeamInfo');
  Dialog.toast('正在为您刷新最新排行榜，请稍等…', 100000);
  $.ajax({
    url: '/api/shop/119/getRank',
    // url: './data.json',
    type: 'GET',
    dataType: 'json',
    success: function (result) {
      Dialog.toast('已成功获取最新排行榜~', 1500);
      $rankTable.empty();
      if (result.data.length === 11) {
        var myTeam = result.data.pop();
        renderData('myTeamTmpl', {
          item: myTeam
        }, $myTeamInfo);
        $('.j_MyTeamInfo').removeClass('hide');
        if (!$('.j_AddTeamTips').hasClass('hide')) {
          $('.j_AddTeamTips').addClass('hide');
        }
      } else {
        $('.j_AddTeamTips').removeClass('hide');
        if (!$('.j_MyTeamInfo').hasClass('hide')) {
          $('.j_MyTeamInfo').addClass('hide');
        }
      }

      renderData('rankListTmpl', {
        data: result.data.map(function (data, index) {
          // data.orderCount = '-';
          data.rankNum = index + 1;
          data.headImg += '.70x70.jpg';
          return data;
        })
      }, $rankTable);

    }
  });
};

CommonwealPage.prototype.eventBind = function () {
  var _self = this;

  $('.j_Refresh').on('touchstart', function () {
    _self.listRank();
  });

  $(document).on('touchstart', '.j_JoinBtn', function () {
    $('.j_JoinCover').removeClass('hide').addClass('see');
    $('.j_CoverTeamName').text($(this).attr('data-teamname'));
    $('.j_CoverAvator').attr('src', $(this).attr('data-avator'));
    $('.j_CoverShopName').text($(this).attr('data-shopname'));
    $('.j_CoverShopId').text('ID:' + $(this).attr('data-id'));
    $('.j_CoverInviteCode').text($(this).attr('data-invite'));
  });

  $('.j_CloseBtn').on('touchstart', function () {
    $('.j_JoinCover').removeClass('see').addClass('un-see');
    setTimeout(function () {
      $('.j_JoinCover').addClass('hide').removeClass('un-see');
    }, 300);
  });

  $(document).on('touchstart', '.j_ShareBtn', function () {
    window.location.href = '/shop/invitationApp?inviteCode=' + $(this).attr('data-invite');
  });
};

CommonwealPage.prototype.init = function () {
  this.countDown119();
  this.pageShare();
  this.listRank();
  this.eventBind();
};

$(document).ready(function () {
  var currentTime = parseInt($('.j_CountDown').attr('data-servertime'));
  var endTime = parseInt($('.j_CountDown').attr('data-endtime'));

  if (currentTime >= endTime) {
    $('.j_EndCover').removeClass('hide');
  }else{
    new CommonwealPage().init();
  }
});



